.switch{
    width: 60%;
    margin: auto;
    margin-bottom: 3rem;
    border: 3px solid var(--mainWhite);
    border-radius: 10px;
}

button{
    width: 50%;
    border: none;
    font-weight: bold;
    padding: 12px 15px;
    text-transform: uppercase;
    cursor: pointer;
}
.on{
    background: var(--mainWhite);
    color: var(--mainBlack);
}

.off{
    background: transparent;
    color: var(--mainWhite);
}